* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
}

body {
    background: #e5f1f8;
    color: #001429;
    overflow-x: hidden;
    font-size: 18px;
    line-height: 1.2;
}

h1 {
    font-family: 'Abel', sans-serif;
    font-size: 150%;
}

h2 {
  font-family: 'Abel', sans-serif;
  font-size: 130%;
}

.app {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto 1fr auto;
  min-height: 100vh;
  width: 100vw;
}

.dropdownHover {
  cursor: pointer;
  position: relative;
}

.dropdownHover:hover + p {
    opacity: 1;
    pointer-events: all;
    display: block;
    height: auto;
}

.dropdownContent {
    position: relative;
    opacity: 0;
    height: 0;
    background: yellow;
    width: inherit;
    transition: opacity 50ms ease-in;
    pointer-events: none;
    border: 1px solid grey;
    z-index: 100;
    background: #E5F1F8;
    font-size: 90%;
    line-height: 90%;
    width: 235px;
}

.dropdownContent a {
  display: block;
}

.dropdownContent:hover {
    opacity: 1;
    pointer-events: all;
}

.dropdownContent::after {
    content: " ";
    position: absolute;
    bottom: 100%;  /* At the top of the tooltip */
    left: 50%;
    margin-left: -5px;
    border-width: 10px;
    border-style: solid;
    border-color: transparent transparent grey transparent;
  }

  .fade-enter {
    opacity: 0;
  }
  
  .fade-enter-active {
    opacity: 1;
    transition: opacity 300ms
  }
  
  .fade-exit {
    opacity: 1;
  }
  
  .fade-exit-active {
    opacity: 0;
    transition: opacity 300ms;
  }